import { Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

export const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'chat_bot',
        loadComponent: () => import('../chat_bot/chat_bot.page').then( m => m.ChatBotPage)
      },
      {
        path: 'import_recipe',
        loadComponent: () =>
          import('../import_recipe/import_recipe.page').then((m) => m.ImportRecipePage),
      },
      {
        path: 'ricette',
        loadComponent: () =>
          import('../ricette/ricette.page').then((m) => m.RicettePage),
      },
      {
        path: 'ricetta',
        loadComponent: () => import('../ricetta/ricetta.page').then( m => m.RicettaPage)
      },
      {
        path: '',
        redirectTo: '/tabs/chat_bot',
        pathMatch: 'full',
      },
    ],
  },
  {
    path: '',
    redirectTo: '/tabs/import_recipe',
    pathMatch: 'full',
  },
];
